import { Component, OnInit } from '@angular/core';
// import { CustomeHttpService } from '../../http/other.http.service';
// import { Commfunc } from '../../../service/commonFun';
import { Message, ConfirmationService } from 'primeng/primeng';
import { format } from 'app/@uisftech/common/service/validator';
import { ApplyBusinessCodeBean, AddApplyBusinessCodeBean, UpdateApplyBusinessCodeBean } from './bean/apply-business-code.bean';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-apply-business-code',
    templateUrl: './apply-business-code.component.html',
    styleUrls: ['./apply-business-code.component.scss'],
    providers: [ConfirmationService, Commfunc]
})

export class ApplyBusinessCodeComponent implements OnInit {
    userformAdd: FormGroup; //校验
    userformUpdate: FormGroup;
    msgs: Message[] = [];//组件
    tableMesg: any = tableMessage;//表格没数据时展示
    code: any;//码值
    applyList: any;//
    first: number = 0;//初始化分页
    updateDisplay: boolean = false;//修改模态框
    addDisplay: boolean = false;//新增模态框
    detailDisplay: boolean = false;//详情模态框
    detailList: any = [];//详情
    MsgBussCode1: any;//应用业务码
    MsgBussName1: any;//应用业务名称
    format: any = format;//校验应用业务码
    statusId: any;//码值表
    applyBusinessCodeBean: ApplyBusinessCodeBean = new ApplyBusinessCodeBean();
    addApplyBusinessCodeBean: AddApplyBusinessCodeBean = new AddApplyBusinessCodeBean();
    updateApplyBusinessCodeBean: UpdateApplyBusinessCodeBean = new UpdateApplyBusinessCodeBean();

    constructor(
        public httpService: CustomeHttpService,
        public commfunc: Commfunc,
        private confirmationService: ConfirmationService,
        public fb: FormBuilder,

    ) {
        this.codeValues();
        this.statusId = this.code['useStatus'];
    }

    ngOnInit() {
        this.userformAdd = this.fb.group({
            'busCodeNameCnh': new FormControl('', [Validators.required,Validators.pattern(/^[\u4e00-\u9fa5]+$/)]),
            'busCodeNameEng': new FormControl('', [Validators.required,Validators.pattern(/^[\w\-]+$/)]),
        });
        this.userformUpdate = this.fb.group({
            'busCodeNameCnh': new FormControl('', [Validators.required,Validators.pattern(/^[\u4e00-\u9fa5]+$/)]),
            'busCodeNameEng': new FormControl('', [Validators.required,,Validators.pattern(/^[\w\-]+$/)]),
        });
        this.query();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    // 初始化
    preSet() {

    }

    //查询
    query() {
        this.httpService.selectMsgBussCode(this.applyBusinessCodeBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.resultList) {
                    this.applyList = data.resultList;
                } else {
                    this.applyList = [];
                }
                this.applyBusinessCodeBean.totalNum = data.total;
            }
            else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询失败';
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
    }

    //新增模态框
    add() {
        this.addDisplay = true;
    }

    //新增
    addApply() {
        //校验的
        for (const i in this.userformAdd.controls) {
            this.userformAdd.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userformAdd.controls) {
            if (!this.userformAdd.controls[i].valid) {
                return;
            }
        }
        // //格式校验
        // if (!this.addApplyBusinessCodeBean.busCodeNameCnh || this.addApplyBusinessCodeBean.busCodeNameCnh == '') {
        //     this.msgs = [];
        //     this.msgs = [{ severity: 'error', summary: '提示', detail: '应用业务名称不能为空！' }];
        //     return false;
        // }
        // if (!this.addApplyBusinessCodeBean.busCodeNameEng || this.addApplyBusinessCodeBean.busCodeNameEng == '' || !format(null, this.addApplyBusinessCodeBean.busCodeNameEng)) {
        //     this.msgs = [];
        //     this.msgs = [{ severity: 'error', summary: '提示', detail: '应用业务码不能为空或不符合格式！' }];
        //     return false;
        // }

        this.httpService.addMsgBussCode(this.addApplyBusinessCodeBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.addDisplay = false;
                this.reset('add');
                this.query();
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: "新增成功" });
            }
            else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '新增失败';
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
    }

    //提交
    submit(flag) {
        switch (flag) {
            case 'add': this.addApply(); break;
            case 'update': this.updateApply(); break;
        }
    }

    //重置
    reset(flag) {
        switch (flag) {
            case 'add':
                this.addApplyBusinessCodeBean.busCodeNameCnh = '';
                this.addApplyBusinessCodeBean.busCodeNameEng = '';
                break;
            case 'update':
                this.updateApplyBusinessCodeBean.busCodeNameCnh = '';
                this.updateApplyBusinessCodeBean.busCodeNameEng = '';
                break;
        }
    }

    //修改模态框
    update(col) {
        console.log(col)
        this.updateDisplay = true;
        this.updateApplyBusinessCodeBean.busCodeNameCnh = col.busCodeNameCnh;
        this.updateApplyBusinessCodeBean.busCodeNameEng = col.busCodeNameEng;
        // this.updateApplyBusinessCodeBean.busCodeId = col.busCodeId;
    }

    //修改
    updateApply() {
        //校验的
        for (const i in this.userformUpdate.controls) {
            this.userformUpdate.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userformUpdate.controls) {
            if (!this.userformUpdate.controls[i].valid) {
                return;
            }
        }
        // //格式校验
        // if (!this.updateApplyBusinessCodeBean.busCodeNameCnh || this.updateApplyBusinessCodeBean.busCodeNameCnh == '') {
        //     this.msgs = [];
        //     this.msgs = [{ severity: 'error', summary: '提示', detail: '应用业务名称不能为空！' }];
        //     return false;
        // }
        // if (!this.updateApplyBusinessCodeBean.busCodeNameEng || this.updateApplyBusinessCodeBean.busCodeNameEng == '' || !format(null, this.addApplyBusinessCodeBean.busCodeNameEng)) {
        //     this.msgs = [];
        //     this.msgs = [{ severity: 'error', summary: '提示', detail: '应用业务码不能为空或不符合格式！' }];
        //     return false;
        // }

        this.httpService.updateMsgBussCode(this.updateApplyBusinessCodeBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.updateDisplay = false;
                this.reset('update');
                this.query();
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: "修改成功" })
            }
            else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '修改失败';
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
    }

    //启用
    startUse(col) {
        this.confirmationService.confirm({
            message: '确认启用该行业务码？',
            accept: () => {
                let params = {
                    busCodeNameEng: col.busCodeNameEng
                }
                this.httpService.enableMsgBussCode(params).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        console.log(data);
                        this.query();
                    }
                    else {
                        this.msgs = [];
                        data.returnCode.message ? data.returnCode.message : '启用失败';
                        this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
                });
            }
        })
    }
    stopUse(col) {
        this.confirmationService.confirm({
            message: '确认停用该行业务码？',
            accept: () => {
                let params = {
                    busCodeNameEng: col.busCodeNameEng
                }
                this.httpService.deleteMsgBussCode(params).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        console.log(data);
                        this.query();
                    }
                    else {
                        this.msgs = [];
                        data.returnCode.message ? data.returnCode.message : '停用失败';
                        this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
                });
            }
        })
    }

    //详情
    detail(col) {
        this.detailDisplay = true;
        this.detailList = [];
        let o;
        for (let k in col) {
            if (col[k] && col[k] != '') {
                o = {
                    label: k,
                    value: col[k]
                }
                switch (k) {
                    case 'busCodeNameEng':
                        o.label = '应用业务码';
                        break;
                    case 'busCodeNameCnh':
                        o.label = '应用业务名称';
                        break;
                    case 'busCodeId':
                        o.label = '应用业务ID';
                        break;
                    case 'createdStamp':
                        o.label = '创建时间';
                        break;
                    case 'creater':
                        o.label = '创建人';
                        break;
                    case 'statusId':
                        o.label = '状态';
                        break;
                    case 'updater':
                        o.label = '更新人';
                        break;
                }
            } else {
                continue;
            }
            this.detailList.push(o);
        }
        console.log(this.detailList);
    }

    //分页
    paginate(event) {
        // console.log(event);
        this.applyBusinessCodeBean.pageSize = event.rows * 1;//每页显示的条数
        let currentPage = event.page + 1;
        this.applyBusinessCodeBean.pageNum = currentPage;//当前页
        this.first = (currentPage - 1) * this.applyBusinessCodeBean.pageSize;
        this.query();//调用查询的方法
    }
}