import { Component, OnInit, Input } from '@angular/core';
import { Commfunc } from '../../../../common/service/commonFun';
import { addCheckCodeInfoTemplateBean } from '../bean/message-template.bean';

@Component({
  selector: 'detail-message-template',
  templateUrl: './detail-message-template.component.html',
  styleUrls: ['./detail-message-template.component.scss']
})
export class DetailMessageTemplateComponent implements OnInit {
  unsubFlag: boolean;
  checkCodeFlag: boolean;
  channelTypes: any = [];
  detailList: any = [];
  templateType: any = [];
  checkType: any = [];
  mesgSendChanellSequ: any[] = [];
  code: any[] = [];
  isMarket: any[] = [];
  @Input() inValue: any;
  addCheckCodeInfoTemplateBean: addCheckCodeInfoTemplateBean = new addCheckCodeInfoTemplateBean();//验证码类消息模板添加
  constructor(
    public commfunc: Commfunc
  ) {
    this.codeValues() //调用方法，获取全部码值

    this.isMarket = this.code['IsMarkt'];
    this.mesgSendChanellSequ = this.code['mesgSendChanellSequ1'];
    this.checkType = this.code['checkType'];
    this.templateType = this.code['MessageTempTotal'];
  }

  ngOnInit() {

    this.channelTypes = this.inValue.channelType.split(',');
    this.detailList = this.inValue;
    if (this.detailList.templateType == '02') {
      this.checkCodeFlag = true;
    } else if (this.detailList.templateType == '03') {
      this.checkCodeFlag = false;
    } else {
      this.checkCodeFlag = false;
    }

    if (this.detailList.isMarket == '0') {
      this.unsubFlag = true;
   } else if (this.detailList.isMarket == '1') {
       this.unsubFlag = false;
   }
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
}
