import { Component, OnInit } from '@angular/core';
import { UpsideTemplateBean } from './bean/upside-template.bean';
import { ConfirmationService, Message } from 'primeng/primeng';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Service } from '../messageCenter.service';

@Component({
  selector: 'app-upside-template',
  templateUrl: './upside-template.component.html',
  styleUrls: ['./upside-template.component.scss'],
  providers:[ConfirmationService,Commfunc,Service]
})
export class UpsideTemplateComponent implements OnInit {
  upsidePeriod: any;
  subSysId: any;
  detailValue: any;
  updateValue: any;
  title: string;
  tableData: any[]=[];
  tableMesg: any = tableMessage;//表格没数据时展示
  first: number=0;
  upsideTemplateBean: UpsideTemplateBean = new UpsideTemplateBean()
  statusId: any[] = [];
  code: any[];
  msgs: Message[] = [];//组件
  total: number;
  dialogModel: number;
  display: boolean;
  bussCodeNameCnh: any[] = [];
  templateName: any[] = [];

  constructor(
    public httpService: CustomeHttpService,
    public commfunc: Commfunc,
    private confirmationService: ConfirmationService,
    public service: Service,
  ) {
    this.codeValues();
    this.statusId = this.code['useStatus'];
    this.subSysId = this.code['subSysId'];
    this.upsidePeriod=this.code['period'];
    this.upsideTemplateBean.pageNum = 1;
    this.upsideTemplateBean.pageSize = 10;
  }

  ngOnInit() {
    this.httpService.selectAllNormalMsgTemplate({ pageNum: 1, pageSize: 100 }).subscribe(data => {
      this.templateName = this.service.changeDrop(data.resultList, 'templateId', 'templateName');
    })
    this.httpService.selectAllNormalMsgBussCode({ pageNum: 1, pageSize: 20 }).subscribe(data => {
      this.bussCodeNameCnh = this.service.changeDrop(data.resultList, 'busCodeNameEng', 'busCodeNameCnh');
    })
    this.query();
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  //查询
  query() {
    this.httpService.selectMsgUpsideService(this.upsideTemplateBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        if(data.resultList){
          this.tableData = data.resultList;
        }else{//若列表为null，修改为[],防止表格一直“数据加载中”
          this.tableData=[];
        }
        
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //点击查询
  queryClick() {
    this.first = 0;
    this.upsideTemplateBean.pageNum = 1;
    this.query();
  }

  //新增
  add() {
    this.display = true;
    this.title = '上行通知服务新增';
    this.dialogModel = 1;
  }

  //新增回调
  addCall(event) {
    if (event.msg) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: '新增成功' })
      this.query();
    }
    this.display = event.display;
  }
  //修改
  update(item) {
    this.display = true;
    this.title = '上行通知服务修改';
    this.dialogModel = 2;
    this.updateValue = item;
  }

  //修改回调
  updateCall(event) {
    if (event.msg) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: '修改成功' });
      this.query();
    }
    this.display = false;
  }
  detail(col){
    this.display=true;
    this.title='上行同志服务详情';
    this.dialogModel=3;
    this.detailValue=col;
  }
  //启用
  startUse(col) {
    this.confirmationService.confirm({
      message: '确认要启用上行服务吗？',
      accept: () => {
        let params = {
          templateId: col.templateId,
          bussCodeNameEng: col.bussCodeNameEng,
        }
        this.httpService.enableMsgUpsideService(params).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: 'success Message', detail: data.returnCode.message });
            this.query();
          }
          else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
          }
        }, err => {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
      }
    })
  }

   //停用
   stopUse(col) {
    this.confirmationService.confirm({
      message: '确认要停用上行服务吗？',
      accept: () => {
        let params = {
          templateId: col.templateId,
          bussCodeNameEng: col.bussCodeNameEng,
        }
        this.httpService.deleteMsgUpsideService(params).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: 'success Message', detail: data.returnCode.message });
            this.query();
          }
          else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
          }
        }, err => {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
      }
    })
  }
  use(col){
    
  }
  //分页
  paginate(event) {
    let rows = event.rows * 1;
    //每页显示的条数
    this.upsideTemplateBean.pageSize = rows;
    //当前页
    let currentPage = event.page + 1;
    this.upsideTemplateBean.pageNum = currentPage;
    this.first = event.rows * event.page
    //调用查询的方法
    this.query();
  }
}
