import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { UpsideTemplateBean } from '../bean/upside-template.bean';
import { Message } from 'primeng/primeng';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'upside-update',
  templateUrl: './upside-update.component.html',
  styleUrls: ['./upside-update.component.scss']
})
export class UpsideUpdateComponent implements OnInit {
  subSysId: any;
  code: any;
  userform: FormGroup;
  upsideTemplateBean: UpsideTemplateBean = new UpsideTemplateBean();
  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  upsidePeriod:any[]=[];
  msgs: Message[] = [];//组件
  constructor(
    public httpService: CustomeHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc
  ) {

    this.codeValues();
    this.subSysId = this.code['subSysId'];
    this.upsidePeriod=this.code['period'];
  }

  ngOnInit() {
    this.upsideTemplateBean = this.inValue;
    this.userform = this.fb.group({
      'templateId': new FormControl('', Validators.required),
      'templateName': new FormControl('', Validators.required),
      'bussCodeNameEng': new FormControl('', Validators.required),
      'bussCodeNameCnh': new FormControl('', Validators.required),
      'period': new FormControl(''),
      'upsideService': new FormControl('', Validators.required),
      'upsideMethod': new FormControl('', Validators.required),
      'upsideInputClass': new FormControl('', Validators.required),
      'subSysId': new FormControl('', Validators.required)
    });
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //提交
  submit() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    this.httpService.updateMsgUpsideService(this.upsideTemplateBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.outValue.emit({ display: false, msg: 'success' });

      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })

  }

  //取消
  cancel() {
    this.outValue.emit({ display: false });
  }
}
