import { Component, OnInit, Input } from '@angular/core';
import { Commfunc } from '../../../../common/service/commonFun';

@Component({
  selector: 'structured-param-detail',
  templateUrl: './structured-param-detail.component.html',
  styleUrls: ['./structured-param-detail.component.scss']
})
export class StructuredParamDetailComponent implements OnInit {
  detailList: any = [];
  code: any = [];
  subSysId: any = [];
  statusId: any = [];
  serviceOption: any = [//是否请求服务填充值 码值
    { label: '请选择', value: '' },
    { label: '是', value: '0' },
    { label: '否', value: '1' }
  ]
@Input() inValue: any;
  constructor(
    public commfunc: Commfunc
  ) {
    this.codeValues();
    this.statusId = this.code['useStatus'];
    this.subSysId = this.code['subSysId'];
  }

  ngOnInit() {
    this.detailList = this.inValue;
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
}
