import { Component, OnInit } from '@angular/core';
import { selectInfoConfineSendRuleBean, deleteInfoConfineSendRuleBean } from './bean/message-rule.bean';
// import { MessageTempTotal1, mesgSendChanellSequ1 } from './../../constant/codeValue';
// import { CustomeHttpService } from './../../http/other.http.service';
import { Message, ConfirmationService } from 'primeng/primeng';
import { ActivatedRoute, Params, Router } from '@angular/router';
import * as _ from 'lodash';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Service } from '../messageCenter.service';
// import { tableMessage } from './../../../../../framework/constant/config';
// import { Commfunc } from "app/pages/tzb/service/commonFun";


@Component({
    selector: 'message-rule-management',
    templateUrl: './message-rule-management.component.html',
    styleUrls: ['./message-rule-management.component.scss'],
    providers: [ConfirmationService, Commfunc,Service],
})
export class MessageRuleManagementComponent implements OnInit {

    templateName: any;
    detailValue: any;
    listArray: any[];
    //公共
    tableMesg: any = tableMessage;
    // 控件
    msgs: Message[] = [];
    first: any = 0;
    // bean
    selectInfoConfineSendRuleBean: selectInfoConfineSendRuleBean = new selectInfoConfineSendRuleBean();
    // 码值
    channelType: any[];
    statusId: any[]
    // 中间量
    messageTempleTotalType: any = '';
    messageSendChanellSequ: any = '';
    //获取所有码值
    code: any;
    detailFlag:boolean=false;
    constructor(
        public httpService: CustomeHttpService,
        public routerInfo: ActivatedRoute,
        public router: Router,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public service:Service
    ) {
        this.codeValues() //调用方法，获取全部码值
        //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
        this.statusId = this.code['useStatus'];
        this.channelType = this.code['mesgSendChanellSequ1'];
    }
    ngOnInit() {
         // 初始化
         this.httpService.selectAllNormalMsgTemplate({ pageNum: 1, pageSize: 20 }).subscribe(data => {
            this.templateName = this.service.changeDrop(data.resultList, 'templateId', 'templateName');
          })
         this.selectInfoConfineSendRuleBean.pageNum = 1;
         this.selectInfoConfineSendRuleBean.pageSize = 10;
        this.preSet();//初始化
        this.selectInfoConfineSendRule();
    }
    preSet() {
       
    }
    doCheck() {
        // 查询
        this.selectInfoConfineSendRuleBean.pageNum = 1;
        this.selectInfoConfineSendRuleBean.pageSize = 10;
        this.first = 0;
        this.selectInfoConfineSendRule();
    }
    //消息限制发送规则查询
    selectInfoConfineSendRule() {

        this.httpService.selectMsgTimeLimit(this.selectInfoConfineSendRuleBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if(data.resultList){
                    this.listArray = data.resultList;
                }else{
                    this.listArray=[];
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });

    }
    windowReSet() {
        // 重置
        this.selectInfoConfineSendRuleBean = new selectInfoConfineSendRuleBean();
        this.selectInfoConfineSendRuleBean.pageNum = 1;
        this.selectInfoConfineSendRuleBean.pageSize = 10;
        this.first = 0;
    }
    add() {
        // 新增
        this.router.navigate(['/pages/tzb/uisftech-view/create-message-rule']);
    }
    upDate(col) {
        // 修改
        let data = JSON.stringify(col);
        this.router.navigate(['/pages/tzb/uisftech-view/update-message-rule', { data: data }]);
    }
    //详情
    detail(col){
        this.detailValue=col;
        this.detailFlag=true;
    }

    startUse(col) {
        // 删除
        this.confirmationService.confirm({
            message: '确定启用该行规则?',
            accept: () => {
                let param={
                    templateId:col.templateId,
                    channelType:col.channelType
                }
                this.httpService.enableMsgTimeLimit(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: '启用成功' }];
                        this.selectInfoConfineSendRule();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
                });
            }
        });
    }
    stopUse(col) {
        // 删除
        this.confirmationService.confirm({
            message: '确定停用该行规则?',
            accept: () => {
                let param={
                    templateId:col.templateId,
                    channelType:col.channelType
                }
                this.httpService.deleteMsgTimeLimit(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: '停用成功' }];
                        this.selectInfoConfineSendRule();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
                });
            }
        });
    }
    deleteInfoConfineSendRule(col) {
        //消息限制发送规则删除
       
    }
    paginate(event) {
        // 分页
        this.selectInfoConfineSendRuleBean.pageSize = event.rows;
        let currentPage = event.page + 1;
        this.selectInfoConfineSendRuleBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.selectInfoConfineSendRuleBean.pageSize;
        this.selectInfoConfineSendRule();
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
}