import { Injectable } from "@angular/core";

@Injectable()
export class Service {
    constructor(){

    }
    changeDrop(list,id,name){
        let newList=[];
        newList.push({label:'请选择',value:''})
        if(list){
            list.forEach(item => {
                let obj={};
                obj['label']=item[name];
                obj['value']=item[id];
                newList.push(obj)
            });
        }
        return newList;
    }
    templateChange(list,item,name){
        console.log(item)
        for(let i=0;i<list.length;i++){
            if(item.value==list[i].value){
                name=list[i].label;
                break
            }
        }
    }
}
