import { Component, OnInit } from '@angular/core';
// import { MessageTempTotal1, mesgSendChanellSequ1, HourTime, MinTime } from './../../../constant/codeValue';
import * as _ from 'lodash';
import { Message, ConfirmationService } from 'primeng/primeng';
import { ActivatedRoute, Params, Router } from '@angular/router';
// import { CustomeHttpService } from './../../../http/other.http.service';
import { addInfoConfineSendRuleBean } from './../bean/message-rule.bean';
// import { Commfunc } from "app/pages/tzb/service/commonFun";
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Service } from '../../messageCenter.service';
@Component({
    selector: 'create-message-rule',
    templateUrl: './create-message-rule.component.html',
    styleUrls: ['./create-message-rule.component.scss'],
    providers: [ConfirmationService, Commfunc, Service],
})
export class CreateMessageRuleComponent implements OnInit {

    limitTimeEnd: string;
    limitTimeStart: string;
    // 码值
    messageTempTotal: any = [];
    mesgSendChanellSequ: any = [];
    hourTime: any = [];
    minTime: any = [];
    userform: FormGroup; //校验

    // 控件
    msgs: Message[] = [];
    // 中间量

    channelType: any[];//渠道类型
    // 限制时间开始
    messageSendTimeLimit1: any = '';//限制时间段开始
    messageSendTimeLimit1Hour: any = '00';//限制时间段开始小时
    messageSendTimeLimit1Min: any = '00';//限制时间段开始分钟
    // 限制时间结束
    messageSendTimeLimit2: any = '';//限制时间段结束
    messageSendTimeLimit2Hour: any = '00';//限制时间段开始小时
    messageSendTimeLimit2Min: any = '00';//限制时间段开始分钟
    // 限制时间拼接
    messageSendTimeLimit: any = '';//限制时间段拼接
    // bean
    addInfoConfineSendRuleBean: addInfoConfineSendRuleBean = new addInfoConfineSendRuleBean();//消息限制发送规则添加
    //获取所有码值
    code: any;
    templateName: any[] = [];
    messageSendTimeLimit1Sec: string='00';
    messageSendTimeLimit2Sec: string='00';
    constructor(
        public httpService: CustomeHttpService,
        public routerInfo: ActivatedRoute,
        public router: Router,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public fb: FormBuilder,
        public service: Service
    ) {
        this.codeValues() //调用方法，获取全部码值
        //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
        this.channelType = this.code['mesgSendChanellSequ1'];
        this.hourTime = this.code['HourTime'];
        this.minTime = this.code['MinTime'];
    }
    ngOnInit() {
        this.userform = this.fb.group({
            'templateId': new FormControl('', [Validators.required]),
            // 'templateName': new FormControl('', [Validators.required]),
            'channelType': new FormControl('', [Validators.required]),
        });

        this.httpService.selectAllNormalMsgTemplate({ pageNum: 1, pageSize: 20 }).subscribe(data => {
            this.templateName = this.service.changeDrop(data.resultList, 'templateId', 'templateName');
        })
    }


    templateChange(item) {
        for (let i = 0; i < this.templateName.length; i++) {
            if (item.value == this.templateName[i].value) {
                this.addInfoConfineSendRuleBean.templateName = this.templateName[i].label;
                break
            }
        }
    }
    submit() {
        //提交
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }

        //时间拼接
        this.addInfoConfineSendRuleBean.limitTimeStart = `${this.messageSendTimeLimit1Hour}:${this.messageSendTimeLimit1Min}:${this.messageSendTimeLimit1Sec}`;
        this.addInfoConfineSendRuleBean.limitTimeEnd = `${this.messageSendTimeLimit2Hour}:${this.messageSendTimeLimit2Min}:${this.messageSendTimeLimit2Sec}`;

        // 时间合理性判断
        if (this.addInfoConfineSendRuleBean.limitTimeStart == this.addInfoConfineSendRuleBean.limitTimeEnd) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '开始和结束时间不能完全一致' }];
            return;
        }
        this.confirmationService.confirm({
            message: '确定是否新增?',
            accept: () => {
                this.addInfoConfineSendRule();
            }
        });
    }

    addInfoConfineSendRule() {
        // 消息限制发送规则添加
        this.httpService.addMsgTimeLimit(this.addInfoConfineSendRuleBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '新增成功' }];
                this.router.navigate(['/pages/tzb/uisftech-view/message-rule-management']);
            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询失败';
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
    }
    windowReSet() {
        // 重置
        this.messageSendTimeLimit1Hour = '00';//限制时间段开始时
        this.messageSendTimeLimit1Min = '00';//限制时间段开始分
        this.messageSendTimeLimit2Hour = '00';//限制时间段结束时
        this.messageSendTimeLimit2Min = '00';//限制时间段结束分
        this.messageSendTimeLimit1Sec = '00';
        this.messageSendTimeLimit2Sec = '00';
        this.addInfoConfineSendRuleBean = new addInfoConfineSendRuleBean();//消息限制发送规则添加
    }
    return() {
        this.router.navigate(['/pages/tzb/uisftech-view/message-rule-management']);
    }

    changeMsgType(event) {
        //消息类型联动渠道类型

    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
}