import { Component, OnInit } from '@angular/core';
import { addSysNotificationInfoTemplateBean, addTheUpsideInfoTemplateBean, addCheckCodeInfoTemplateBean, selectMsgBusiCodeBean, selectMsgBusiCodeStructMapBean } from './../bean/message-template.bean';
// import { isSinChanell, reseTimes, IsMarkt1, mesgSendChanellSequ, busCodeEnName, checkCodeLeng, checkType } from './../../../constant/codeValue';
import { Message, ConfirmationService } from 'primeng/primeng';
import { ActivatedRoute, Params, Router } from '@angular/router';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { specialSymbol, maxNum } from 'app/@uisftech/common/service/validator';
import { Service } from '../../messageCenter.service';

@Component({
    selector: 'create-message-template',
    templateUrl: './create-message-template.component.html',
    styleUrls: ['./create-message-template.component.scss'],
    providers: [ConfirmationService, Commfunc, Service],

})
export class CreateMessageTemplateComponent implements OnInit {
    unsubFlag: boolean = false;
    period: any;
    // 页面显示
    // 参数中间量
    arr: any = [];//结构化参数中间数组
    bCEnName2: any = 'D-CheckCode';//应用业务码英文名
    checkCodeLengths: string='' ;//验证码长度
    checkCodeType: any ;//验证码类型

    // 控件
    msgs: Message[] = [];
    // 码值
    IsMarkt: any = [];//是否营销类
    mesgSendChanellSequ: any = [];//消息发送渠道顺序
    checkCodeLeng: any = [];//验证码长度
    checkType: any = [];//验证码类型

    addCheckCodeInfoTemplateBean: addCheckCodeInfoTemplateBean = new addCheckCodeInfoTemplateBean();//验证码类消息模板添加

    userform: FormGroup;
    userform1: FormGroup;
    userform2: FormGroup;
    code: any;


    channelType: string;
    channelTypes: any[] = [];
    isMarket: any[] = [];
    templateType: any[] = [];
    msgContent: string = '';
    msgContentChosed: string;
    bussCodeNameEng: any[] = [];
    checkCodeFlag: boolean = false;
    smsTempFlag: boolean = false;
    constructor(
        public httpService: CustomeHttpService,
        public routerInfo: ActivatedRoute,
        public router: Router,
        public confirmationService: ConfirmationService,
        public fb: FormBuilder,
        public commfunc: Commfunc,
        public service: Service
    ) {
        this.codeValues() //调用方法，获取全部码值
        //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
        // this.busCodeEnName=this.code['busCodeEnName'];
        this.isMarket = this.code['IsMarkt1'];
        this.mesgSendChanellSequ = this.code['mesgSendChanellSequ'];
        this.checkCodeLeng = this.code['checkCodeLeng'];
        this.checkType = this.code['checkType'];
        this.templateType = this.code['MessageTempTotal'];
        this.period = this.code['period'];
    }
    ngOnInit() {
        this.selectMsgBusiCodeStructMap()
        this.preSet()
    }

    preSet() {
        // 初始化
        this.userform = this.fb.group({
            'templateId': new FormControl('', [Validators.required, Validators.pattern(/^[\d]+$/)]),
            'templateName': new FormControl('', [Validators.required, specialSymbol]),//模板名称
            'isMarket': new FormControl('', [Validators.required]),
            'templateType': new FormControl('', [Validators.required]),
            'msgContent': new FormControl('', [Validators.required]),
            'description': new FormControl('', [Validators.required]),
            'smsPlatTempNum': new FormControl('', [Validators.required, specialSymbol]),
        });
        this.typeChange();
    }

    typeChange() {
        if (this.addCheckCodeInfoTemplateBean.templateType == '02') {
            this.checkCodeFlag = true;
            // this.unsubFlag = false;
            // this.smsTempFlag = true;
            this.addCheckCodeInfoTemplateBean.smsPlatTempNum='4101'
        } else if (this.addCheckCodeInfoTemplateBean.templateType == '03') {
            // this.unsubFlag = true;
            this.checkCodeFlag = false;
            // this.smsTempFlag = true;
            this.addCheckCodeInfoTemplateBean.smsPlatTempNum='9019'
        } else {
            this.checkCodeFlag = false;
            // this.unsubFlag = true;
            // this.smsTempFlag = true;
            this.addCheckCodeInfoTemplateBean.smsPlatTempNum='9019'
        }
    }


    unSubscribeFlagChange(){
        if (this.addCheckCodeInfoTemplateBean.isMarket == '0') {
           this.unsubFlag = true;
        } else if (this.addCheckCodeInfoTemplateBean.isMarket == '1') {
            this.unsubFlag = false;
        }
    }

    //结构化参数查询
    selectMsgBusiCodeStructMap() {
        let param = {
            bussCodeNameEng: this.bCEnName2
        }
        this.httpService.selectAllNormalMsgStructParam(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let dataList = data.resultList;
                for (let i in dataList) {
                    this.arr.push({ label: dataList[i].structParamNameCnh, value: dataList[i].structParamNameEng });
                }
            } else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询消息业务结构化字段映射关系失败';
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
    }


    //结构化参数点击选中
    clickSpan(event, i) {
        let ele = event.target.parentNode.children
        this.msgContentChosed = `[${this.arr[i].value}]`;
        for (let i = 0; i < ele.length; i++) {
            ele[i].className = 'spanNoClick';
        }
        event.target.className = 'spanNoClick spanClick';
    }


    //右移
    changeToRight() {
        this.msgContent += this.msgContentChosed;
    }

    //提交
    submit() {

        if (this.checkCodeFlag) {
            if (this.checkCodeLengths == '' || this.addCheckCodeInfoTemplateBean.checkCodeType == '' || this.addCheckCodeInfoTemplateBean.period =='') {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '验证码类型、长度、有效期不能为空！' })
                return
            }
        }
        if(this.unsubFlag){
            if(this.addCheckCodeInfoTemplateBean.unSubscribeFlag==''){
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '退订标识不能为空！' })
                return
            }
        }
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        if (this.channelTypes.length == 0) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '渠道类型不能为空！' }];
            return
        }
       
        this.addCheckCodeInfoTemplateBean.channelType = this.channelTypes.join(',');
        this.addCheckCodeInfoTemplateBean.msgContent = this.msgContent;
        this.addCheckCodeInfoTemplateBean.checkCodeLength = Number(this.checkCodeLengths);
        this.httpService.addMsgTemplate(this.addCheckCodeInfoTemplateBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.router.navigate(['/pages/tzb/uisftech-view/message-template-maintenance']);
            }
            else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '添加失败';
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
    }

    reset() {
        this.addCheckCodeInfoTemplateBean = new addCheckCodeInfoTemplateBean();
        this.msgContent = '';
    }
    return() {
        this.router.navigate(['/pages/tzb/uisftech-view/message-template-maintenance']);
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
}