import { Component, OnInit } from '@angular/core';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'framework-special-post',
    template: `
    <div class="ui-g-12 container">
        <div class="ui-g-12">
            <div class="ui-g-4" [formGroup]="userform">
                <label appValidation class="label1 text-r">调整内容:</label>
                <p-dropdown class="dropdown1" [options]="AdjustContent" [(ngModel)]="adjustContent" formControlName="adjustContent" name="adjustContent"></p-dropdown>
                <div style="color:red;text-align:center" *ngIf="!userform.controls['adjustContent'].valid&&userform.controls['adjustContent'].dirty">
                    <p [hidden]="!userform.hasError('required','adjustContent')"> 调整内容不能为空！</p>
                </div>
            </div>
        </div>
        <div class="ui-g-12">
            <div class="ui-g-4">
                <label class="label1 text-r">浏览附件:</label>
                <input class="input1" type="text" pInputText readonly>
            </div>
            <div class="ui-g-1">
                <button class="btn_color" pButton (click)="downLoadTemp()" label="下载模板"></button>
            </div>
            <div class="ui-g-4">
                <p-fileUpload class="fileUpload" name="myfile[]" url="{{UPLOAD}}" accept=".xls" multiple="multiple" invalidFileTypeMessageDetail="只能上传Excel" chooseLabel="浏览" uploadLabel="上传" cancelLabel="取消" (onUpload)="onUpload($event)"></p-fileUpload>
            </div>
        </div>
    </div>
    <p-growl [(value)]="msgs"></p-growl>`,
    styles: [`
        .container {
            background: #fff;
        }
        .label1 {
            display: inline-block;
            width: 30%;
        }
        .text-r {
            text-align: right;
        }
        .input1 {
            display: inline-block;
            width: 60%;
        }
        .btn_color {
            background: linear-gradient(#ffc107, #ffc107);
        }
        :host/deep/ .dropdown1 .ui-dropdown {
            width: 60% !important;
        }
        :host/deep/ .fileUpload .ui-fileupload .ui-fileupload-buttonbar.ui-widget-header.ui-corner-top {
            background: none;
            padding: 0;
        }
        :host/deep/ .fileUpload .ui-widget-header {
            background: none;
        } 
    `],
    providers: [Commfunc]
})

export class SpecialPostComponent implements OnInit {

    //公共
    msgs: Message[] = [];
    code: any;//码值

    //
    UPLOAD: string = TZB_CUS_UPLOAD;
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;
    files: any = [];
    name: string;
    fileList: any = [];
    AdjustContent: any;
    adjustContent: string;//调整内容
    userform: FormGroup; //校验

    constructor(
        public commfunc: Commfunc,
        public customeHttpService: CustomeHttpService,
        public fb: FormBuilder
    ) {
        this.codeValues() //调用方法，获取全部码值
        this.AdjustContent = this.code['AdjustContent'];//调整内容
    }

    ngOnInit() {
        this.userform = this.fb.group({
            'adjustContent': new FormControl('', [Validators.required]),
        });
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    //上传
    onUpload(event) {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }

        debugger
        this.files = [];
        for (let file of event.files) {
            this.files.push(file)
        }
        this.name = this.files[0].name;
        if (event.xhr.response) {
            let response = JSON.parse(event.xhr.response);
            if (response.hasOwnProperty('errorMsg')) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: response.errorMsg });
                return
            }

            this.fileList = response[0];
        }
        if (this.fileList) {
                let param = {
                    fileUrl: this.fileList.fileUrl,
                    fileName: this.fileList.fileName,
                    // fileUrl: 'D:\\',
                    // fileName: 'updateCustInfo.xls',
                    updateType: this.adjustContent,
                }
                this.customeHttpService.batchSpecialPostUpdate(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        console.log(data);

                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: '调用服务成功' }];
                    } else {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                    }
                });
        }
    }

    //下载模板
    downLoadTemp(){
        open(this.DOWNLOAD + "?fileName=" + encodeURI("updateCustInfo.xls") + "&fileUrl=" + "/copdata/moban/", "_blank");
    }

}

