import { Component, OnInit } from '@angular/core';
import { Message, ConfirmationService } from 'primeng/primeng';
import { StructuredParameterBean, AddStructuredParameterBean, UpdateStructuredParameterBean } from './bean/structured-parameter.bean';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'app-structured-parameter',
    templateUrl: './structured-parameter.component.html',
    styleUrls: ['./structured-parameter.component.scss'],
    providers: [ConfirmationService, Commfunc]
})

export class StructuredParameterComponent implements OnInit {

    detailFlag: boolean = false;
    detailValue: any;
    subSysId: any[] = [];
    msgs: Message[] = [];//组件
    tableMesg: any = tableMessage;//表格没数据时展示
    code: any;//码值
    first: number = 0;//初始化分页
    isShow: boolean;//判断是否请求获取
    isShowUpdate: boolean;
    structuredList: any;//结构化字段数组
    option: any = [//表格-是否请求服务填充值 码值
        { label: '是', value: '0' },
        { label: '否', value: '1' }
    ]
    serviceOption: any[] //是否请求服务填充值 码值


    applyOption: any = [//应用业务 码值
        { label: '请选择', value: '' },
        { label: '是', value: '1' },
        { label: '否', value: '0' }
    ]
    userform: FormGroup; //校验
    userformUpdate: FormGroup;
    updateDisplay: boolean = false;//修改模态框
    addDisplay: boolean = false;//新增模态框
    structuredParameterBean: StructuredParameterBean = new StructuredParameterBean();
    addStructuredParameterBean: AddStructuredParameterBean = new AddStructuredParameterBean();
    updateStructuredParameterBean: UpdateStructuredParameterBean = new UpdateStructuredParameterBean();
    statusId: any[];
    constructor(
        public httpService: CustomeHttpService,
        public commfunc: Commfunc,
        private confirmationService: ConfirmationService,
        public fb: FormBuilder
    ) {
        this.codeValues();
        this.statusId = this.code['useStatus'];
        this.subSysId = this.code['subSysId'];
        this.serviceOption = [//是否请求服务填充值 码值
            { label: '请选择', value: '' },
            { label: '是', value: '0' },
            { label: '否', value: '1' }
        ]
        this.addStructuredParameterBean.isMsgcentDeal = '1';

    }

    ngOnInit() {
        // this.applyQuery();
        // this.userform = this.fb.group({
        //     'structParamNameCnh': new FormControl('', [Validators.required, Validators.pattern(/^[\u4e00-\u9fa5]+$/)]),
        //     'structParamNameEng': new FormControl('', [Validators.required, Validators.pattern(/^[\w\-]+$/)]),
        //     'isMsgcentDeal': new FormControl('', [Validators.required])
        // });
        // this.userformUpdate = this.fb.group({
        //     'structParamNameCnh': new FormControl('', [Validators.required, Validators.pattern(/^[\u4e00-\u9fa5]+$/)]),
        //     'structParamNameEng': new FormControl('', [Validators.required, Validators.pattern(/^[\w\-]+$/)]),
        //     'isMsgcentDeal': new FormControl('', [Validators.required])
        // });
        // if(this.isShowUpdate){
        //     this.showFunc(this.userformUpdate);
        // }
        this.query();
        // this.templeteChange();
        // this.templeteChangeUpdate();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    showFunc(userform) {
        userform.addControl('subSysId', this.fb.control('', [Validators.required, Validators.pattern(/^\w+$/)]));
        userform.addControl('structParamReqInputCnh', this.fb.control('', [Validators.required, Validators.pattern(/^[\u4e00-\u9fa5]+$/)]));
        userform.addControl('structParamReqInputEng', this.fb.control('', [Validators.required, Validators.pattern(/^\w+$/)]));
        userform.addControl('structParamServiceNameCnh', this.fb.control('', [Validators.required, Validators.pattern(/^[\u4e00-\u9fa5]+$/)]));
        userform.addControl('structParamServiceNameEng', this.fb.control('', [Validators.required, Validators.pattern(/^\w+$/)]));
        userform.addControl('structParamMethodNameCnh', this.fb.control('', [Validators.required, Validators.pattern(/^[\u4e00-\u9fa5]+$/)]));
        userform.addControl('structParamMethodNameEng', this.fb.control('', [Validators.required, Validators.pattern(/^\w+$/)]));
        userform.addControl('structParamInputBean', this.fb.control('', [Validators.required]));
        userform.addControl('serviceInputNameCnh', this.fb.control('', [Validators.required, Validators.pattern(/^[\u4e00-\u9fa5]+$/)]));
        userform.addControl('serviceInputNameEng', this.fb.control('', [Validators.required, Validators.pattern(/^\w+$/)]));
        userform.addControl('serviceOutputNameCnh', this.fb.control('', [Validators.required, Validators.pattern(/^[\u4e00-\u9fa5]+$/)]));
        userform.addControl('serviceOutputNameEng', this.fb.control('', [Validators.required, Validators.pattern(/^\w+$/)]));
    }

    noShowFunc(userform) {
        userform.removeControl('subSysId');
        userform.removeControl('structParamReqInputCnh');
        userform.removeControl('structParamReqInputEng');
        userform.removeControl('structParamServiceNameCnh');
        userform.removeControl('structParamServiceNameEng');
        userform.removeControl('structParamMethodNameCnh');
        userform.removeControl('structParamMethodNameEng');
        userform.removeControl('structParamInputBean');
        userform.removeControl('serviceInputNameCnh');
        userform.removeControl('serviceInputNameEng');
        userform.removeControl('serviceOutputNameCnh');
        userform.removeControl('serviceOutputNameEng');
    }
    showInit(userform) {
        userform.removeControl('structParamNameCnh');
        userform.removeControl('structParamNameEng');
        userform.removeControl('isMsgcentDeal');
    }
    //下拉-是否请求获取
    templeteChange() {
        if (this.addStructuredParameterBean.isMsgcentDeal == '0') {
            this.isShow = true;
            this.showFunc(this.userform);
        } else {
            this.isShow = false;
            this.noShowFunc(this.userform);
        }
    }
    templeteChangeUpdate() {
        if (this.updateStructuredParameterBean.isMsgcentDeal == '0') {
            this.isShowUpdate = true;
            this.showFunc(this.userformUpdate);
        } else {
            this.isShowUpdate = false;
            this.noShowFunc(this.userformUpdate);
        }
    }
    addClose() {
        this.showInit(this.userform);
        this.showFunc(this.userform);
    }
    updateClose() {
        this.showInit(this.userformUpdate);
        this.showFunc(this.userformUpdate);
    }
    //应用业务码查询
    applyQuery() {
        let params = {
            msgStructParamList: []
        };
        this.httpService.selectMsgStructParamList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.resultList && data.resultList.length != 0) {
                    this.applyOption = [{ label: '请选择', value: '' }];
                    data.resultList.forEach(element => {
                        let o = {
                            label: element.busCodeChinessName,
                            value: element.busCodeEnglishName
                        }
                        this.applyOption.push(o);
                    });
                }
                // console.log(this.applyOption);
            }
            else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询失败';
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
    }

    //查询
    query() {
        // console.log(this.structuredParameterBean);
        this.httpService.selectMsgStructParam(this.structuredParameterBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.resultList) {
                    this.structuredList = data.resultList;
                } else {
                    this.structuredList = [];
                }
                this.structuredParameterBean.totalNum = data.total;
            }
            else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询失败';
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
    }

    //新增模态框
    add() {
        this.userform = this.fb.group({
            'structParamNameCnh': new FormControl('', [Validators.required, Validators.pattern(/^[\u4e00-\u9fa5]+$/)]),
            'structParamNameEng': new FormControl('', [Validators.required, Validators.pattern(/^[\w\-]+$/)]),
            'isMsgcentDeal': new FormControl('', [Validators.required])
        });
        this.addDisplay = true;
        this.isShow = false;
        this.reset('add');


        this.templeteChange();

    }

    //新增
    addStructured() {

        // 校验页面数据
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        // if (this.isShow) {
        //     for (let i in this.addStructuredParameterBean) {
        //         if (this.addStructuredParameterBean[i] == '') {
        //             this.msgs = [{ severity: 'error', summary: '提示', detail: "字段不能为空！" }];
        //             return
        //         }
        //     }
        // }
        this.httpService.addMsgStructParam(this.addStructuredParameterBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.addDisplay = false;
                this.query();
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: "新增成功" });
            }
            else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '新增失败';
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
    }
    //详情
    detail(col) {
        this.detailValue = col;
        this.detailFlag = true;
    }
    //删除
    startUse(col) {
        this.confirmationService.confirm({
            message: '确认要启用构化字段吗？',
            accept: () => {
                let params = {
                    structParamNameEng: col.structParamNameEng
                }
                this.httpService.enableMsgStructParam(params).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.query();
                    }
                    else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
                });
            }
        })
    }

    stopUse(col) {
        this.confirmationService.confirm({
            message: '确认要停用构化字段吗？',
            accept: () => {
                let params = {
                    structParamNameEng: col.structParamNameEng
                }
                this.httpService.deleteMsgStructParam(params).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.query();
                    }
                    else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
                });
            }
        })
    }

    //重置
    reset(flag) {
        switch (flag) {
            case 'add':
                for (let k in this.addStructuredParameterBean) {
                    this.addStructuredParameterBean[k] = '';
                }
                break;
            case 'update':
                for (let k in this.updateStructuredParameterBean) {
                    if (k != this.updateStructuredParameterBean.structParamNameEng || k != this.updateStructuredParameterBean.structParamNameCnh) {
                        this.updateStructuredParameterBean[k] = '';
                    }
                }
                break;
        }
    }

    //修改模态框
    update(col) {
        this.userformUpdate = this.fb.group({
            'structParamNameCnh': new FormControl('', [Validators.required, Validators.pattern(/^[\u4e00-\u9fa5]+$/)]),
            'structParamNameEng': new FormControl('', [Validators.required, Validators.pattern(/^[\w\-]+$/)]),
            'isMsgcentDeal': new FormControl('', [Validators.required])
        });
        // if (this.isShowUpdate) {
        //     this.showFunc(this.userformUpdate);
        // }
        for (let k in col) {
            this.updateStructuredParameterBean[k] = col[k];
        }
        this.updateDisplay = true;
        this.templeteChangeUpdate();

        
       
        // if (this.updateStructuredParameterBean.isMsgcentDeal == '0') {
        //     this.isShowUpdate = true;
        // } else {
        //     this.isShowUpdate = false;
        // }

    }

    //修改
    updateStructured() {
        // 校验页面数据
        for (const i in this.userformUpdate.controls) {
            this.userformUpdate.controls[i].markAsDirty();
        }
        for (const i in this.userformUpdate.controls) {
            if (!this.userformUpdate.controls[i].valid) {
                return;
            }
        }
        // if(this.isShow){
        //     for(let i in this.updateStructuredParameterBean){
        //         if(this.updateStructuredParameterBean[i]==''){
        //             this.msgs=[{ severity: 'error', summary: '提示', detail: "字段不能为空！" }];
        //             return
        //         }
        //     }
        // }
        this.httpService.updateMsgStructParam(this.updateStructuredParameterBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.updateDisplay = false;
                this.reset('update');
                this.query();
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: "修改成功" });
            }
            else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '修改失败';
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
    }

    //提交
    submit(flag) {
        if (this.addStructuredParameterBean.isMsgcentDeal == '1') {
            this.addStructuredParameterBean.structParamReqInputCnh = '';
            this.addStructuredParameterBean.structParamReqInputEng = '';
            this.addStructuredParameterBean.subSysId = '';
            this.addStructuredParameterBean.structParamServiceNameCnh = '';
            this.addStructuredParameterBean.structParamServiceNameEng = '';
            this.addStructuredParameterBean.structParamMethodNameCnh = '';
            this.addStructuredParameterBean.structParamMethodNameEng = '';
            this.addStructuredParameterBean.structParamInputBean = '';
            this.addStructuredParameterBean.serviceInputNameCnh = '';
            this.addStructuredParameterBean.serviceInputNameEng = '';
            this.addStructuredParameterBean.serviceOutputNameCnh = '';
            this.addStructuredParameterBean.serviceOutputNameEng = '';
        }
        if (this.updateStructuredParameterBean.isMsgcentDeal == '1') {
            this.updateStructuredParameterBean.structParamReqInputCnh = '';
            this.updateStructuredParameterBean.structParamReqInputEng = '';
            this.updateStructuredParameterBean.subSysId = '';
            this.updateStructuredParameterBean.structParamServiceNameCnh = '';
            this.updateStructuredParameterBean.structParamServiceNameEng = '';
            this.updateStructuredParameterBean.structParamMethodNameCnh = '';
            this.updateStructuredParameterBean.structParamMethodNameEng = '';
            this.updateStructuredParameterBean.structParamInputBean = '';
            this.updateStructuredParameterBean.serviceInputNameCnh = '';
            this.updateStructuredParameterBean.serviceInputNameEng = '';
            this.updateStructuredParameterBean.serviceOutputNameCnh = '';
            this.updateStructuredParameterBean.serviceOutputNameEng = '';
        }
        switch (flag) {
            case 'add': this.addStructured(); break;
            case 'update': this.updateStructured(); break;
        }
    }

    //分页
    paginate(event) {
        this.structuredParameterBean.pageSize = event.rows * 1;//每页显示的条数
        let currentPage = event.page + 1;
        this.structuredParameterBean.pageNum = currentPage;//当前页
        this.first = (currentPage - 1) * this.structuredParameterBean.pageSize;
        this.query();//调用查询的方法
    }
}