import { Component, OnInit, Input } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'detail-message-rule',
  templateUrl: './detail-message-rule.component.html',
  styleUrls: ['./detail-message-rule.component.scss']
})
export class DetailMessageRuleComponent implements OnInit {
  statusId: any;
  channelType: any;
  code: any;
  detailList: any;
  @Input() inValue: any;
  constructor(
    public commfunc: Commfunc
  ) {
    this.codeValues() //调用方法，获取全部码值
    this.statusId = this.code['useStatus'];
    this.channelType = this.code['mesgSendChanellSequ1'];
  }

  ngOnInit() {
    this.detailList = this.inValue;
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
}
