import { Component, OnInit } from '@angular/core';
import { selectInfoTemplateByConditionsBean, deleteInfoTemplateBean } from './bean/message-template.bean';
// import { CustomeHttpService } from './../../http/other.http.service';
import * as _ from 'lodash';
import { Message, ConfirmationService } from 'primeng/primeng';
import { ActivatedRoute, Params, Router } from '@angular/router';
// import { tableMessage } from './../../../../../framework/constant/config';
// import { specialSymbol, checkNumber, checkZzNum } from './../../../service/validator';
import { Validators, FormControl, FormGroup, FormBuilder, AbstractControl } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { specialSymbol } from 'app/@uisftech/common/service/validator';
// import { Commfunc } from "app/pages/tzb/service/commonFun";


@Component({
    selector: 'message-template-maintenance',
    templateUrl: './message-template-maintenance.component.html',
    styleUrls: ['./message-template-maintenance.component.scss'],
    providers: [ConfirmationService, Commfunc],

})
export class MessageTemplateMaintenanceComponent implements OnInit {

    total: any;
    //公共
    tableMesg: any = tableMessage;
    // 码值
    templateTypes: any[]; //消息大类
    isMarkts: any[]; //是否营销类

    // 中间量
    // templateId: any = '';
    // templateName: any = '';
    // messageTempleTotalType: any = '';
    // messageTempleSubType: any = '';
    // isMarket: any = '';
    channelTypes: any[] = [];//渠道
    // channelType: string;
    // smsPlatTempNum: string;//消息模板编号
    statusIds: any[] = [];
    // statusId: string;
    // templateType: string;
    // 控件
    msgs: Message[] = [];
    first: any = 0;
    //bean
    selectInfoTemplateByConditionsBean: selectInfoTemplateByConditionsBean = new selectInfoTemplateByConditionsBean();//消息模板条件查询
    deleteInfoTemplateBean: deleteInfoTemplateBean = new deleteInfoTemplateBean();//消息模板删除
    //校验
    userform1: FormGroup;
    // userform2: FormGroup;
    userId: string;
    code: any;
    listArray: Array<any> = [];//消息模板列表
    detailFlag: boolean = false;
    detailValue: any;
    constructor(
        public httpService: CustomeHttpService,
        public routerInfo: ActivatedRoute,
        public router: Router,
        public confirmationService: ConfirmationService,
        public fb: FormBuilder,
        public commfunc: Commfunc
    ) {
        this.codeValues();
        this.templateTypes = this.code['MessageTempTotal'];
        this.isMarkts = this.code['IsMarkt'];
        this.statusIds = this.code['useStatus'];
        this.channelTypes = this.code['mesgSendChanellSequ1'];
    }
    ngOnInit() {
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        this.preSet();//初始化
        this.selectInfoTemplateByConditions();
    }
    preSet() {
        // 初始化
        //表单校验
        this.userform1 = this.fb.group({
            'templateId': new FormControl('', [Validators.maxLength(16), specialSymbol]),//模板编号
            'templateName': new FormControl('', [Validators.maxLength(60), specialSymbol]),//模板名称
        });
        // this.userform2 = this.fb.group({
        //     'messageTempleTotalType': new FormControl('', [ Validators.maxLength(2)]),//消息大类
        //     'messageTempleSubType': new FormControl('', [ Validators.maxLength(255), specialSymbol]),//消息小类
        // });
        this.selectInfoTemplateByConditionsBean.pageNum = 1;
        this.selectInfoTemplateByConditionsBean.pageSize = 10;
        // this.isMarketingTemple = '';//默认设置            
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    reSet() {
        //重置
        // this.templateId = '';
        // this.templateName = '';
        // this.isMarket = '';
        // this.channelType = '';
        // this.smsPlatTempNum = '';
        // this.statusId = '';
        // this.templateType = '';
        this.selectInfoTemplateByConditionsBean = new selectInfoTemplateByConditionsBean();
        this.first = 0;
        this.selectInfoTemplateByConditionsBean.pageNum = 1;
        this.selectInfoTemplateByConditionsBean.pageSize = 10;

    }
    doCheck() {
        // 查询
        //校验的
        for (const i in this.userform1.controls) {
            this.userform1.controls[i].markAsDirty();
        }
        // for (const i in this.userform2.controls) {
        //     this.userform2.controls[i].markAsDirty();
        // }
        //处理是否提交
        for (const i in this.userform1.controls) {
            if (!this.userform1.controls[i].valid) {
                return;
            }
        }
        // for (const i in this.userform2.controls) {
        //     if (!this.userform2.controls[i].valid) {
        //         return;
        //     }
        // }

        // let flag1 = true;
        // flag1 = this.isNull();//非空校验
        // if (!flag1) {
        //     return;
        // }
        // 查询条件赋值
        this.selectInfoTemplateByConditionsBean.pageNum = 1;
        this.selectInfoTemplateByConditionsBean.pageSize = 10;
        this.first = 0;
        this.selectInfoTemplateByConditions();
    }
    selectInfoTemplateByConditions() {
        // 消息模板条件查询
        this.httpService.selectMsgTemplate(this.selectInfoTemplateByConditionsBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.resultList) {
                    let listArray = this.channel(data.resultList)
                    this.listArray = listArray;
                    this.total=data.total;
                } else {
                    this.listArray = [];
                }
            }
            else {
                this.msgs = [];
                data.returnCode.message ? data.returnCode.message : '查询失败';
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        });
    }
    channel(list) {
        let arr = ['01', '02', '03', '04', '05'];
        let arr1 = ['短信', '微信', '站内信', 'App消息流', '邮件'];
        list.forEach(item => {
            let channel = '';
            let channelList = item.channelType.split(',');
            arr.forEach((e, i) => {
                if (channelList.indexOf(e) != -1) {
                    channel += `${arr1[i]},`;
                }
            })
            item['channelTypeCN'] = channel.slice(0, channel.length - 1);
        });
        return list;
    }
    paginate(event) {
        // 分页
        this.selectInfoTemplateByConditionsBean.pageSize = event.rows;
        let currentPage = event.page + 1;
        this.selectInfoTemplateByConditionsBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.selectInfoTemplateByConditionsBean.pageSize;
        this.selectInfoTemplateByConditions();// 查询
    }
    windowReSet() {
        // 页面重置
        this.reSet();
    }
    // 其他
    // isNull() {
    //     // 非空校验

    //     return true;
    // }
    add() {
        // 跳转到新增页面
        this.router.navigate(['/pages/tzb/uisftech-view/create-message-template']);
    }
    upDate(col) {
        let param = JSON.stringify(col)
        console.log(param)
        this.router.navigate(['/pages/tzb/uisftech-view/update-message-template', { params: param }]);
        // if (col.userId==this.userId) {
        //     // 跳转修改页面
        //     this.router.navigate(['/pages/tzb/other/update-message-template', { templeId: col.templeId, messageTempleTotalType: col.messageTempleTotalType }]);           
        // }else{
        //     this.msgs = [{ severity: 'error', summary: '提示', detail: '不能修改他人模板' }];
        // }
    }
    //详情
    detail(col) {
        this.detailValue = col;
        this.detailFlag = true;
    }
    //启用
    startUse(col) {

        this.confirmationService.confirm({
            message: '确定要启用该行模板?',
            accept: () => {
                let param = {
                    templateId: col.templateId
                }
                this.httpService.enableMsgTemplate(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: '启用成功' }];
                        this.selectInfoTemplateByConditions();
                    }
                    else {
                        this.msgs = [];
                        data.returnCode.message ? data.returnCode.message : '启用失败';
                        this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
                });
            }
        });
    }
    stopUse(col) {

        this.confirmationService.confirm({
            message: '确定要停用该行模板?',
            accept: () => {
                let param = {
                    templateId: col.templateId
                }
                this.httpService.deleteMsgTemplate(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: '停用成功' }];
                        this.selectInfoTemplateByConditions();
                    }
                    else {
                        this.msgs = [];
                        data.returnCode.message ? data.returnCode.message : '停用失败';
                        this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
                });
            }
        });
    }

    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}