import { Component, OnInit, Input } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'upside-detail',
  templateUrl: './upside-detail.component.html',
  styleUrls: ['./upside-detail.component.scss']
})
export class UpsideDetailComponent implements OnInit {
  subSysId: any;
  code: any;
  statusId: any;
  @Input() inValue: any;
  detailList:any=[];
  constructor(
    public commfunc:Commfunc
  ) { 
    this.codeValues();
    this.statusId = this.code['useStatus'];
    this.subSysId = this.code['subSysId'];
  }

  ngOnInit() {
    this.detailList=this.inValue;
    //console.log(this.inValue)
  }
//码值
codeValues() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code = ret //code是自己声明的变量
}
}
