import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//消息中心
import { MessageCenterComponent } from 'app/@uisftech/view/message-center/message-center.component';
//消息模板管理
import { MessageTemplateMaintenanceComponent } from 'app/@uisftech/view/message-center/message-template-maintenance/message-template-maintenance.component';
//创建消息模板
import { CreateMessageTemplateComponent } from 'app/@uisftech/view/message-center/message-template-maintenance/create-message-template/create-message-template.component';
//修改消息模板
import { UpdateMessageTemplateComponent } from 'app/@uisftech/view/message-center/message-template-maintenance/update-message-template/update-message-template.component';
//消息规则管理
import { MessageRuleManagementComponent } from 'app/@uisftech/view/message-center/message-rule-management/message-rule-management.component';
//消息规则新增
import { CreateMessageRuleComponent } from 'app/@uisftech/view/message-center/message-rule-management/create-message-rule/create-message-rule.component';
//消息规则修改
import { UpdateMessageRuleComponent } from 'app/@uisftech/view/message-center/message-rule-management/update-message-rule/update-message-rule.component';
import { CustomGeneral } from 'app/pages/tzb/custom/custom-information/custom-change/custom-general/custom-general.component';
import { CheckComponent } from 'app/@uisftech/view/check/check.component';
import { ApplyBusinessCodeComponent } from 'app/@uisftech/view/message-center/apply-business-code/apply-business-code.component';
import { UpsideTemplateComponent } from 'app/@uisftech/view/message-center/upside-template/upside-template.component';
import { StructuredParameterComponent } from 'app/@uisftech/view/message-center/structured-parameter/structured-parameter.component';
import { SpecialPostComponent } from './specialPost/specialPost.component';

export const routes: Routes = [
  {
    path: 'check',
    component: CheckComponent,
    children: [
      { path: 'valuton', loadChildren: './routing/valuton/valuton.module#OtherValutonModule' },
      { path: 'introduce', loadChildren: './routing/introduce/introduce.module#IntroduceModule' },
      { path: 'current', loadChildren: './routing/current/current.module#CurrentModule' },
      { path: 'cusGroup', loadChildren: './routing/cusGroup/cusGroup.module#OtherCusGroupModule' },
      { path: 'myCustomMess', loadChildren: './routing/myCustomMess/myCustomMess.module#OtherCustMessModule' },
      { path: 'voucher', loadChildren: './routing/voucher/voucher.module#VoucherModule1' },
      { path: 'message', loadChildren: './routing/message/message.module#OtherMessageModule' },
      { path: 'marking', loadChildren: './routing/marking/marking.module#OtherMarkingModule' },
      { path: 'cusMarking', loadChildren: './routing/cusMarking/cusMarking.module#OtherCusMarkingModule' },
      { path: 'customMindApply', loadChildren: './routing/customMindApply/customMindApply.module#OtherCustomMindApplyModule' },
      { path: 'cusContact', loadChildren: './routing/cusContact/cusContact.module#OtherCusContactModule' },
      { path: 'cuslistDownload', loadChildren: './routing/custListDown/custListDown.module#OtherCustListDownModule' },
      { path: 'relation', loadChildren: './routing/relation/relation.module#RelationModule' }
    ]
  },
  {
    path: 'message-center', component: MessageCenterComponent,//消息中心
  },
  {
    path: 'message-template-maintenance', component: MessageTemplateMaintenanceComponent,
  },
  {
    path: 'create-message-template', component: CreateMessageTemplateComponent,
  },
  {
    path: 'update-message-template', component: UpdateMessageTemplateComponent,
  },

  {
    path: 'message-rule-management', component: MessageRuleManagementComponent,
  },
  {
    path: 'create-message-rule', component: CreateMessageRuleComponent,
  },
  { path: 'apply-business-code', component: ApplyBusinessCodeComponent },
  { path: 'upside-template', component: UpsideTemplateComponent },
  { path: 'structure-paramet', component: StructuredParameterComponent },
  {
    path: 'update-message-rule', component: UpdateMessageRuleComponent,
  },
  { path: 'custom-general', component: CustomGeneral },
  { path: 'workdesk', loadChildren: './workdesk/workdesk.module#workDeskModule' },
  { path: 'special-post', component: SpecialPostComponent }




]


export const UisftechViewRouting: ModuleWithProviders = RouterModule.forChild(routes);
