import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { UpsideTemplateBean } from '../bean/upside-template.bean';
import { Message } from 'primeng/primeng';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Service } from '../../messageCenter.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'upSide-add',
  templateUrl: './upSide-add.component.html',
  styleUrls: ['./upSide-add.component.scss'],
  providers: [Service]
})
export class UpSideAddComponent implements OnInit {
  subSysId: any;
  code: any;
  bussCodeNameCnh: any[] = [];
  templateName: any[] = [];
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  upsideTemplateBean: UpsideTemplateBean = new UpsideTemplateBean();
  msgs: Message[] = [];//组件
  userform: FormGroup;
  upsidePeriod:any[]=[];
  constructor(
    public httpService: CustomeHttpService,
    public fb: FormBuilder,
    public service: Service,
    public commfunc: Commfunc
  ) {

    this.codeValues();

    this.subSysId = this.code['subSysId'];
    this.upsidePeriod=this.code['period'];
  }

  ngOnInit() {
    this.userform = this.fb.group({
      'templateId': new FormControl('', Validators.required),
      // 'templateName': new FormControl('', Validators.required),
      'bussCodeNameEng': new FormControl('', Validators.required),
      // 'bussCodeNameCnh': new FormControl('', Validators.required),
      'period': new FormControl(''),
      'upsideService': new FormControl('', Validators.required),
      'upsideMethod': new FormControl('', Validators.required),
      'upsideInputClass': new FormControl('', Validators.required),
      'subSysId': new FormControl('', Validators.required)
    });
    this.httpService.selectAllNormalMsgTemplate({ pageNum: 1, pageSize: 100 }).subscribe(data => {
      //console.log(data)
      this.templateName = this.service.changeDrop(data.resultList, 'templateId', 'templateName');
    })
    this.httpService.selectAllNormalMsgBussCode({ pageNum: 1, pageSize: 20 }).subscribe(data => {
      this.bussCodeNameCnh = this.service.changeDrop(data.resultList, 'busCodeNameEng', 'busCodeNameCnh');
      //console.log(this.bussCodeNameCnh)
    })

  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  templateChange(item) {
    for (let i = 0; i < this.templateName.length; i++) {
      if (item.value == this.templateName[i].value) {
        this.upsideTemplateBean.templateName = this.templateName[i].label;
        break
      }
    }
  }
  businessCodeChange(item) {
    for (let i = 0; i < this.bussCodeNameCnh.length; i++) {
      if (item.value == this.bussCodeNameCnh[i].value) {
        this.upsideTemplateBean.bussCodeNameCnh = this.bussCodeNameCnh[i].label;
        break
      }
    }
  }
  //提交
  submit() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    this.httpService.addMsgUpsideService(this.upsideTemplateBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.outValue.emit({ display: false, msg: 'success' });

      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })

  }


  //取消
  cancel() {
    this.outValue.emit({ display: false });
  }
}

